.header-color {
  background: #000000;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  
  top: o;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(http://www.same-same.com/images/back2.jpg);
}

.projects-grid{

  display: flex;
  margin-top: 120px;
  width: 100%;
  background: url(http://www.same-same.com/images/back2.jpg);
}

/*  resume page css */

.resume-right-col{
  background: url(http://www.same-same.com/images/back2.jpg);
  color: white;
  padding: 2em;
}


/* contact page css */

.contact-body{
  margin: auto;
  background: url(http://www.same-same.com/images/back2.jpg);
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  
}
.contact-grid{
  opacity: 0.8;
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
  
}

.contact-grid h2{
  
  font-family: 'Times New Roman', Times, serif;
}

.contact-grid p{
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list{
  
  font-size: 50px;
  padding-left: 1rem;
}

.contact-list i{
  width: 100px;
}

.avatar-img{
  height: 200px;
  padding-top: .001em;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 15%;
  border-radius: 20px;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
  
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
  align-content: center;
}
.banner-text p{
  margin-left: 40px;
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
  
}
.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}
